a, a > span {
  position: relative;
  color: inherit;
  text-decoration: none;
  line-height: 24px;
  cursor: none;
}

a:before, a:after, a > span:before, a > span:after {
  content: '';
  position: absolute;
  transition: transform .5s ease;
}

.hoverEffect {
  display: inline-block;
  overflow: hidden;
  opacity: 0.6;
}

.hoverEffect:before {
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: white;
  transform: translateX(-110%);
}

.hoverEffect:after {
  content: attr(data-content);
  left: 0;
  top: 0;
  width: 0;
  margin: 12px;
  color: white;
  white-space: nowrap;
  overflow: hidden;
  transition: width .5s ease;
}

.hoverEffect > span {
  display: block;
  margin: 2px;
  padding: 10px;
  background: black;
}

.hoverEffect:hover:before {
  transform: translateX(0);
}

.hoverEffect:hover:after {
  width: calc(100% - 24px);
}

.hoverEffect:hover  {
  opacity: 1;
}

